import React from "react";
import wrapper from "../commons/WebComponentWrapper";
import "carbon-components/scss/globals/scss/styles.scss";

const dispatchedEvents = [];

const Container = function(props, ref) {
    return view(props, ref);
}

const view = (props, ref) => {
    return (<div ref={ref} style={props.styles}> 
        <slot/>
    </div>);
}

const styles = `

`

const props = [{
    name: "styles",
    type: "object",
    defaultValue: {}
}];

const WrappedComponent = wrapper(React.forwardRef(Container), props);
export {WrappedComponent, styles, props, dispatchedEvents};